.collapse-normal{
    max-width: 1023px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 auto;
    margin-bottom: 30px;
    cursor: pointer;
}
.collapse-header-normal{
    background-color: #FF6060;
    display: flex;
    flex-direction: row;
    color: white;
    max-width: 100%;
    justify-content: space-between;
    align-items: center;
    font-size: 24px;
    padding-left: 18px;
    padding-right: 33px;
    height: 47px;
    border-radius: 5px;
    margin-left: 20px;
    margin-right: 20px;
}

.collapse-text-normal{
    color: #FF6060;
    font-size: 24px;
    background-color: #F6F6F6;
    max-width: 100%;
    padding: 30px;
    line-height: 150%;
    border-radius: 0 0 5px 5px;
    margin-left: 20px;
    margin-right: 20px;
}

.direction-vector {
    width: 31px;
    height: 31px;
  }
  .direction-vector.rotate {
    transform: rotate(180deg);
    transition: 0.3s ease;
  }


  @media (max-width: 800px) {
    .collapse-normal{
        margin-left: 20px;
        margin-right: 20px;
        margin-bottom: 20px;
    }
    .collapse-header-normal{
        font-size: 13px;
        padding-left: 11px;
        padding-right: 33px;
        height: 30px;
        border-radius: 5px;
    }
    
    .collapse-text-normal{
        font-size: 12px;
        background-color: #F6F6F6;
        max-width: 100%;
        padding: 11px 20px;
        padding-top: 20px;
        padding-bottom: 40px;
        line-height: 140%;
    }
    .direction-vector {
        width: 18px;
        height: 18px;
      }
    .direction-vector.rotate {
        transform: rotate(180deg);
        transition: 0.3s ease;
      }
  }