@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

* {
    margin: 0;
    font-family: 'Montserrat', sans-serif;
}

.header-container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    max-width: 1240px;
    margin: 20px auto;
    align-items: center;
}

.header-tile{
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    width: 210px;
    justify-content: space-between;
    padding-left: 20px;
}
.nav-part{
    padding-right: 20px;
}
.nav-ul {
    display: flex;
    flex-direction: row;
    list-style: none;
    gap: 60px;
}

.link-pages {
    font-size: 24px;
    color:  #FF6060;
    text-decoration: none;
}
.link-pages:hover {
    text-decoration: underline;
}
nav .active {
    text-decoration: underline;
}

@media (max-width: 570px) {
    .header-container{
        min-width: 335px;

    }
    .header-tile{
        width: 145px;
        height: 43px;
        padding-top: 20px;
    }
    
    .nav-ul {
        gap: 10px;
    }
    
    .link-pages {
        font-size: 12px;
    }
}